<template>
  <div>
    <b-table striped hover small :items="modules" :fields="fields"></b-table>
    <div class="text-center" v-if="loading">
        <b-spinner class="cuja" label="Spinning"></b-spinner>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase'
import router from 'vue-router'
import axios from 'axios'

export default {
  name: 'Students',
  data () {
    return {
      profile: {},
      loading: false,
      modules: [],
      fields: {
          UID: {
            label: 'UID',
            sortable: true
          },
          last: {
            label: 'Last Name',
            sortable: true
          },
          first: {
            label: 'First name',
            sortable: false
          },
          city: {
            label: 'City',
            sortable: true
          },
          organisation: {
            label: 'Partner',
            sortable: true
          }
        }
    }
  },
  methods: {
    getModules(){
        const functions = firebase.functions()
        this.toggleLoading()
        axios.get('https://hooks.zapier.com/hooks/catch/4921789/jwcqvb/').then(res => {
          console.log('Zapier Call: ', res)
        })
    },
    toggleLoading(){
          this.loading = !this.loading
      }
  },
  created(){
    this.getModules()
  }
}
</script>

<style scoped>
</style>



